import {Link} from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import {Row, Col, Container, Nav} from "react-bootstrap"
import monogramLogo from "../images/monogram.png"
import {FaFacebookF, FaTwitter, FaInstagram, FaYoutube, FaSteam} from 'react-icons/fa';
import MainLogo from "../images/mainLogo.png";


const Footer = () => (
    <footer>
      <div className="footer-links d-flex align-items-center justify-content-around p-2">
        <div className="text-center py-3">
          <Link to="/">
            <img
                src={monogramLogo}
                className="footer-logo"
                height="100"
                alt="everreachmonogramlogo"/>
          </Link>
        </div>
        <div className="text-center">
          Subscribe to our newsletter!
          <form className="form-inline mt-2">
            <input className="form-control mr-sm-2" type="text" placeholder="Enter Email" aria-label="Search"/>
            <button className="btn btn-outline-success" type="submit">Subscribe</button>
          </form>
        </div>
        <div>
        <span className="header-icon">
          <a href='https://www.instagram.com/everreachfarm/' target="_blank">
            <FaInstagram/>
          </a>
        </span>
          <span className="header-icon">
          <a href='https://www.facebook.com/everreachfarm' target="_blank">
            <FaFacebookF/>
          </a>
        </span>
        </div>
      </div>
      <div className="text-center">© All Rights Reserved Ever Reach Farm 2020</div>
    </footer>
)


export default Footer
